import { MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { __ } from '@wordpress/i18n';
import {
  Button,
  PanelBody,
} from '@wordpress/components';

export function MultiImageSelect(props) {
  const {
    attributes,
    label
  } = props;
  const {
    mediaID,
    media
  } = attributes;

  //URL 
  const getImages = (media) => {
    //
    let imagesArray = media.map((image) => {
      return (
        <figure>
          <img
            src={image.url}
            className="image"
            alt=""
          />
          <figcaption className="block-image-caption">
            {image.caption ? image.caption : ''}
          </figcaption>
        </figure>
      );
    });
    return imagesArray;
  }

  //
  const getImageButton = (open) => {
    if (media.length > 0) {
      return (
        <div onClick={open} className="block-container">
          {getImages(media)}
        </div>
      )
    }
    else {
      return (
        <div className="button-container">
          <Button
            onClick={open}
            className="button button-large"
          >
            {__("Image Upload", 'slide-blocks')}
          </Button>
        </div>
      );
    }
  };

  return (
    <PanelBody
      title={label}
      initialOpen={true}
      className='itmar_image_display'
    >
      <MediaUploadCheck>
        <MediaUpload
          multiple={true}
          gallery={true}  //
          onSelect={(media) => props.onSelectChange(media)}
          allowedTypes={['image']}
          value={mediaID}
          render={({ open }) => getImageButton(open)}
        />
      </MediaUploadCheck>
      {media.length != 0 && //
        <MediaUploadCheck>
          <Button
            onClick={() => props.onAllDelete()}
            variant="secondary"
            isDestructive
            className="removeImage">
            {__("Delete All", 'slide-blocks')}
          </Button>
        </MediaUploadCheck>
      }
    </PanelBody>
  );
}